body {
	line-height: 1.6;
	margin: 0;
	min-height: 100vh;
}

ul {
  margin: 0 5%;
  padding: 0;
  list-style: none;
}

.logo {
	margin: 0;
}

.main-nav {
	margin-top: 5px;

}
.logo a,
.main-nav a {
	padding: 10px 15px;
	text-align: center;
	display: block;
}

.main-nav a {
	color: #34495e;
	font-size: .99em;
}

.main-nav a:hover {
	color: #718daa;
}



.header {
	padding-top: .5em;
    padding-bottom: .5em;
	border: 1px solid #a2a2a2;
	background-color: #f4f4f4;
	box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.75);
	border-radius: 5px;
}

@media (min-width: 769px) {
	.header,
	.main-nav {
		display: -webkit-flex;
		display: flex;
	}
	.header {
		-webkit-flex-direction: column;
		        flex-direction: column;
		-webkit-align-items: center;
		        align-items: center;    
		width: 100%;
		margin: -101px 0;
    }
    .logo {
        margin: 0 5%;
    }

}

@media (min-width: 1025px) {
	.header {
		-webkit-flex-direction: row;
		        flex-direction: row;
        -webkit-justify-content: space-between;
                justify-content: space-between;
	}

}
.natedintro {
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),url(/nated/static/media/landing-background.eeea3d18.jpg);
    background-size: cover;
    background-position: center;
    min-height: 50%;
    color: white;
    padding: 20% 0;
    text-align: center;
}

.companyname {
    color: white;
}

@media (min-width: 513px) {
    .natedintro > div {
        margin: 0 10%;
    }
}
