    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-content: stretch;
            align-content: stretch;
  }
  
.referenceworks {
    padding: 80px 0;
}
  
.box {
    padding: 1%;
}
  
.box img {
    width: 100%;
    height: auto;
    margin-bottom: 15px;
    transition: all 0.3s;
}

.box:hover img {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}
