body {
	line-height: 1.6;
	margin: 0;
	min-height: 100vh;
}

ul {
  margin: 0 5%;
  padding: 0;
  list-style: none;
}

.logo {
	margin: 0;
}

.main-nav {
	margin-top: 5px;

}
.logo a,
.main-nav a {
	padding: 10px 15px;
	text-align: center;
	display: block;
}

.main-nav a {
	color: #34495e;
	font-size: .99em;
}

.main-nav a:hover {
	color: #718daa;
}



.header {
	padding-top: .5em;
    padding-bottom: .5em;
	border: 1px solid #a2a2a2;
	background-color: #f4f4f4;
	-webkit-box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.75);
	-moz-box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.75);
	box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.75);
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	border-radius: 5px;
}

@media (min-width: 769px) {
	.header,
	.main-nav {
		display: flex;
	}
	.header {
		flex-direction: column;
		align-items: center;    
		width: 100%;
		margin: -101px 0;
    }
    .logo {
        margin: 0 5%;
    }

}

@media (min-width: 1025px) {
	.header {
		flex-direction: row;
        justify-content: space-between;
	}

}